body {
    background-color: #fff; }
.icon {
    width: 100%;
    height: 100%;
    fill: #fff; }
.app-badge {
    width: 100%;
    height: 100vh;
    background-color: #fff;
    padding: 2%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    &__container {
        width: 100%;
        height: 100%;
        background-color: $blue;
        position: relative;
        border: none;
        border-radius: 1%;
        &_inner {
            width: 100%;
            @include vcenter; } }
    &__btn_close {
        margin-top: 2%;
        margin-right: 2%;
        margin-bottom: 2%;
        width: 5%;
        height: 5%;
        background-color: transparent;
        float: right; }
    &__message {
        color: #fff;
        text-align: center;
        clear: right;
        margin: 0 25px;
        &_mobile {
            font-family: $museosanscyrl_500;
            font-size: 18px;
            @include css-lock(18, 24, 320, 414); }
        &_tablet {
            font-family: $museosanscyrl_700;
            font-size: 24px;
            @include css-lock(24, 36, 768, 1024); } }
    &__image {
        @include cover;
        background-size: contain;
        margin: 5vh auto;
        &_android-mob, &_ios-mob {
            width: 175px;
            height: 355px;
            @include rh(320) {
                height: 125px; }
            @include rh(375) {
                height: 175px; }
            @include rh(425) {
                height: 155px; }
            @include rh(550) {
                height: 195px; } }
        &_windows-mob {
            width: 195px;
            height: 335px;
            @include rh(320) {
                height: 135px; }
            @include rh(360) {
                height: 145px; }
            @include rh(425) {
                height: 200px; }
            @include rh(550) {
                height: 225px; } }
        &_android-tab {
            width: 395px;
            height: 550px;
            @include rh(329) {
                height: 145px; }
            @include rh(550) {
                height: 175px; }
            @include rh(768) {
                height: 245px; }
            @include rh(1024) {
                height: 300px; } }
        &_ios-tab {
            width: 387px;
            height: 540px;
            @include rh(414) {
                height: 185px; }
            @include rh(620) {
                height: 225px; }
            @include rh(768) {
                height: 300px; }
            @include rh(1024) {
                height: 325px; } }
        &_windows-tab {
            width: 332px;
            height: 542px;
            @include rh(414) {
                height: 165px; }
            @include rh(550) {
                height: 175px; }
            @include rh(768) {
                height: 245px; }
            @include rh(1024) {
                height: 300px; } } }
    &__btn_open {
        font-family: $museosanscyrl_900;
        font-size: 17px;
        @include css-lock(17, 24, 320, 1024);
        color: $gray;
        padding: 16px 45px;
        background-color: $yellow;
        margin: 0 auto;
        border: none;
        border-radius: 30px;
        display: block; } }
